.button {
    padding: 5px 24px;
    width: 129px;
    height: 30px;
    border-radius: 3px;
}

.disabled {
    background: #004d99;
    color: #002e5c;
    opacity: 0.5;
    }

.secondary {
    border: 1px solid #06c;
    width: 176px;
    color: #06c;
}

.default {
    background: #06c;
    color: #fff;
   }

.red {
    background: #cc1439;
    color: #fff;
   }

.default:hover {
    background: #0080ff;
}

.red:hover {
    background: #ff1a47;
}

.secondary:hover {
    background: #e5f0fa;
}


.default:active {
    background: #0059b2;
}

.secondary:active {
    background: #c9e5ff;
}

.red:active {
    background: #b21232;
}

.button:disabled {
    background: #004d99;
    opacity: 0.5;
}
